import React from 'react';
import { StyleSheet, Text, View} from 'react-native';
import { Button} from 'react-native-elements';
import Icon from 'react-native-vector-icons/FontAwesome';
import { useNavigation } from '@react-navigation/native';

export class RedScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      //defauilt value of the date time
      date: '',
    };
  }
  componentDidMount() {
    var that = this;
    var date = new Date().getDate(); //Current Date
    var month = new Date().getMonth() + 1; //Current Month
    var year = new Date().getFullYear(); //Current Year
    var hours = new Date().getHours(); //Current Hours
    var min = new Date().getMinutes(); //Current Minutes
    var sec = new Date().getSeconds(); //Current Seconds
    that.setState({
      //Setting the value of the date time
      date:
        date + '/' + month + '/' + year + ' ' + hours + ':' + min + ':' + sec,
    });
  }
render() {
    const { navigation } = this.props;

return (
<View style={styles.container}>
<Text
          style={{
            fontSize: 20,
            marginTop: 16,
            marginLeft: 50,
            marginRight: 30,
            alignSelf: 'flex-start',
            fontWeight: 'bold'
          }}>
          {this.state.date}
        </Text>
<Text style={{ fontSize: 30, marginLeft: 50, marginRight: 30, marginTop:20, marginBottom:40}}> Based on your response(s) to the screening questions, <Text style={{fontWeight:'bold'}}> do not enter the office </Text> and stay home. Please stay in communication with your supervisor, and contact Human Resources. </Text>
<View>
<Button
icon={
<Icon
  name="undo"
  size={80}
  color="#2cc5ef"
/>
}
type='clear'
onPress={() => navigation.popToTop()}
/>
<Text> Redo Screening Questions </Text>
</View>
</View>
);
}
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',

    fontSize: 80,

  },
});
