import React from 'react';
import { StyleSheet, Text, View, Linking} from 'react-native';
import { Button} from 'react-native-elements';
import Icon from 'react-native-vector-icons/FontAwesome';
import { useNavigation } from '@react-navigation/native';

export class GreenScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      //defauilt value of the date time
      date: '',
    };
  }
  componentDidMount() {
    var that = this;
    var date = new Date().getDate(); //Current Date
    var month = new Date().getMonth() + 1; //Current Month
    var year = new Date().getFullYear(); //Current Year
    var hours = new Date().getHours(); //Current Hours
    var min = new Date().getMinutes(); //Current Minutes
    var sec = new Date().getSeconds(); //Current Seconds
    that.setState({
      //Setting the value of the date time
      date:
        date + '/' + month + '/' + year,
    });
  }
render() {
  const { navigation } = this.props;
return (
  <View style={styles.container}>
  <Text
            style={{
              fontSize: 20,
              marginTop: 16,
              marginLeft: 70,
              marginRight: 30,
              alignSelf: 'flex-start',
              fontWeight: 'bold',
              color:'#fff'
            }}>
            {this.state.date}
          </Text>
  <Text style={{ fontSize: 30, marginLeft: 50, marginRight: 30, marginTop:20, marginBottom:40, color:'#fff'}}> Based on your responses to the screening questions, you are approved to enter the office on <Text style={{fontWeight:'bold'}}> {this.state.date} </Text>  </Text>
  <View style={{marginBottom: 80}}>
  <Button
  icon={
  <Icon
    name="undo"
    size={80}
    color="#fff"
  />
  }
  type='clear'
  onPress={() => navigation.popToTop()}
  />
  <Text style={{color:"#fff"}} > Redo Screening Questions </Text>
  </View>
  <View style={{flexDirection:'row'}}>
<Button onPress={() => Linking.openURL('mailto:reception@hhangus.com?subject=Passed Result &body=This employee has passed the screening questions today') }
      title="Send Results"
      buttonStyle={{backgroundColor:"#fff"}}
      titleStyle={{color:"#a7e0a5"}}/>
      <Button
      icon={
      <Icon
        name="info-circle"
        size={20}
        color="#fff"
      />
      }
      type='clear'
      onPress={() => navigation.navigate('Results Info')}
      />
</View>
</View>
);
}
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#A7E0a5',
    alignItems: 'center',
    justifyContent: 'center',
    fontSize: 80,
  },
});
